import React from 'react'
import './GlassWindow.css'

export default class GlassWindow extends React.Component{
  render() {
    return (
      <div className='glasswindow-wrapper' style={{width:this.props.width,height:this.props.height}}>
        {/* <div className="x" onClick={()=>{}}>X</div> */}
        <h2>随机移动的彩色方块</h2>
        <div>created on 2020-7-10 by react.js</div>
      </div>
    )
        
  }
}