import React from 'react'
import { connect } from 'react-redux'
import GlassWindow from '../GlassWindow/GlassWindow'
import { increment, decrement } from '../../redux/actions'
// import { counter } from '../../redux/reducers'

class Counter extends React.Component{
  state = {
    count: 0
  }
  btnstyle = {width: '40px',height:'20px'}
  slotstyle = {position:'absolute',left:'50px',top:'80px'}
  btnstyle2= {width: '40px',height:'20px',position:'absolute',left:'700px'}
  btnstyle3= {width: '40px',height:'20px',position:'absolute',left:'750px'}
  render() {
    let slot = (<div style={this.slotstyle}>
                  <p style={{fontSize:'30px',marginBottom:'10px'}}> 用计数器学习redux </p>
                  <p style={{marginBottom:'10px'}}>我是计数器{this.props.count}</p>
                  <button style={this.btnstyle}  onClick={()=>{ this.props.increment(2)}}>+</button>
                  <button style={this.btnstyle}  onClick={()=>{ this.props.decrement(2)}}>-</button>
                </div>)
    let slot2 = (<div >
                  <button style={this.btnstyle2}  onClick={()=>{ this.props.increment(2)}}>+</button>
                  <button style={this.btnstyle3}  onClick={()=>{ this.props.decrement(2)}}>-</button>
                </div>)
    return(
        <div className='counter-wrapper'>
          <GlassWindow width='600px' height='400px'></GlassWindow>
          {/* {slot} */}
          {/* {slot2} */}
        </div>
    )
  }
}

export default connect(state => ({ count: state }), { increment, decrement })(Counter)